___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --maxw: 1200px;
  --ebnf-bg: #f8f9fa;
  --ebnf-border: #e1e4e8;
  --ebnf-text: #24292e;
  --ebnf-header-bg: #f1f3f5;
}

body {
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  margin: 0;
}

header {
  padding: 24px 16px;
  border-bottom: 1px solid #ddd;
}

main {
  max-width: var(--maxw);
  margin: 0 auto;
  padding: 16px;
}

nav {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 12px;
}

nav a {
  text-decoration: none;
  border: 1px solid #ccc;
  padding: 6px 10px;
  border-radius: 8px;
  color: #111;
}

nav a:hover {
  background: #f5f5f5;
  border-color: #999;
}

/* ===== Collapsible Sections ===== */

.grammar-section {
  padding: 0;
  border-top: 1px solid #eee;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 16px 8px;
  background: transparent;
  border: none;
  cursor: pointer;
  text-align: left;
  font-family: inherit;
}

.section-header:hover {
  background: #f8f8f8;
}

.section-header h2 {
  margin: 0;
  font-size: 1.4em;
}

.section-chevron {
  font-size: 12px;
  transition: transform 0.2s ease;
  color: #666;
}

.section-chevron.expanded {
  transform: rotate(90deg);
}

.rule-count {
  color: #666;
  font-size: 14px;
  font-weight: normal;
}

.section-content {
  padding: 0 8px 16px;
}

/* ===== Toolbar ===== */

.toolbar {
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: center;
}

.toolbar input {
  width: min(480px, 100%);
  padding: 10px 12px;
  border: 1px solid #ccc;
  border-radius: 10px;
  font-size: 14px;
}

.toolbar input:focus {
  outline: none;
  border-color: #0066cc;
  box-shadow: 0 0 0 2px rgba(0, 102, 204, 0.2);
}

.toolbar-actions {
  display: flex;
  gap: 8px;
}

.toolbar-btn {
  padding: 8px 14px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background: #fff;
  cursor: pointer;
  font-size: 13px;
  color: #333;
  transition: all 0.15s ease;
}

.toolbar-btn:hover {
  background: #f5f5f5;
  border-color: #999;
}

.toolbar-btn:active {
  background: #eee;
}

/* ===== Rule Styles ===== */

.rule {
  margin: 14px 0 22px;
}

.rule h3 {
  margin: 0 0 8px;
  font-size: 16px;
  font-weight: 650;
}

.rule .svgwrap {
  overflow-x: auto;
  padding-bottom: 6px;
}

footer {
  padding: 16px;
  border-top: 1px solid #ddd;
  color: #444;
  font-size: 13px;
}

code {
  background: #f5f5f5;
  padding: 2px 6px;
  border-radius: 6px;
}

.subtitle {
  margin-top: 8px;
  color: #444;
}

/* ===== EBNF Definition Styles ===== */

.ebnf-container {
  margin-top: 12px;
  border: 1px solid var(--ebnf-border);
  border-radius: 8px;
  overflow: hidden;
  background: var(--ebnf-bg);
}

.ebnf-toggle {
  cursor: pointer;
  padding: 8px 12px;
  background: var(--ebnf-header-bg);
  font-size: 13px;
  font-weight: 600;
  color: #586069;
  user-select: none;
  list-style: none;
  display: flex;
  align-items: center;
  gap: 6px;
}

.ebnf-toggle::-webkit-details-marker {
  display: none;
}

.ebnf-toggle::before {
  content: "▶";
  font-size: 10px;
  transition: transform 0.15s ease;
}

details[open] > .ebnf-toggle::before {
  transform: rotate(90deg);
}

.ebnf-toggle:hover {
  background: #e6e8eb;
}

.ebnf-code {
  margin: 0;
  padding: 12px 16px;
  font-family: "SF Mono", "Fira Code", "JetBrains Mono", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 13px;
  line-height: 1.5;
  color: var(--ebnf-text);
  overflow-x: auto;
  white-space: pre;
  background: var(--ebnf-bg);
  border-top: 1px solid var(--ebnf-border);
}

/* Highlight optional markers */
.ebnf-code {
  tab-size: 4;
}

/* ===== Dark mode support ===== */

@media (prefers-color-scheme: dark) {
  :root {
    --ebnf-bg: #1e1e1e;
    --ebnf-border: #333;
    --ebnf-text: #d4d4d4;
    --ebnf-header-bg: #252526;
  }

  body {
    background: #121212;
    color: #e0e0e0;
  }

  header {
    border-bottom-color: #333;
  }

  nav a {
    border-color: #444;
    color: #e0e0e0;
  }

  nav a:hover {
    background: #2a2a2a;
    border-color: #666;
  }

  .grammar-section {
    border-top-color: #333;
  }

  .section-header:hover {
    background: #1e1e1e;
  }

  .section-header h2 {
    color: #e0e0e0;
  }

  .section-chevron,
  .rule-count {
    color: #888;
  }

  .toolbar input {
    background: #1e1e1e;
    border-color: #444;
    color: #e0e0e0;
  }

  .toolbar input:focus {
    border-color: #4da6ff;
    box-shadow: 0 0 0 2px rgba(77, 166, 255, 0.2);
  }

  .toolbar-btn {
    background: #2a2a2a;
    border-color: #444;
    color: #e0e0e0;
  }

  .toolbar-btn:hover {
    background: #333;
    border-color: #666;
  }

  .toolbar-btn:active {
    background: #444;
  }

  footer {
    border-top-color: #333;
    color: #aaa;
  }

  code {
    background: #2a2a2a;
  }

  .ebnf-toggle:hover {
    background: #333;
  }
}

/* ===== Responsive adjustments ===== */

@media (max-width: 600px) {
  header {
    padding: 16px 12px;
  }

  .toolbar {
    flex-direction: column;
    align-items: stretch;
  }

  .toolbar input {
    width: 100%;
  }

  .toolbar-actions {
    justify-content: flex-start;
  }

  .rule h3 {
    font-size: 14px;
  }

  .ebnf-code {
    font-size: 12px;
    padding: 10px 12px;
  }

  .section-header h2 {
    font-size: 1.2em;
  }
}
